.header{
  padding: 16px 78px;
  background-color: #fff;
  position: fixed;
  z-index: 1;
  /* border: 5px solid indigo; */
  width: 100%;
  box-shadow: 0 1px 1px #ccc;
  display: flex;
}
.logo{
  width: 130px;
  font-size: 32px;
  font-weight: 1000px;
  color: #0084ff;
  font-family: arial black;
  padding-bottom: 0%;
 /*  position: absolute;
  top: 0.2%; */
} 
.span1{
  width: 135px;
  display: inline-block;
}
.span2{
  display: inline-block;
  margin-top: 20px;
  margin-left: 100px;
 
}
.stories{
  font-weight: 500;
  font-size: 23px;
  font: raleway;
  font-style: italic;
  padding-top:0%;
  position: absolute;
  bottom: 13px;
  }
  .techies{
    margin-top: 10px;
    margin-left:300px;
    width: 300px;
  }
  .button{
    border-radius: 50px;
    width: 140px;
    height: 45px;
    background-color: #0084ff;
    color: #fff;
    border: none;
    margin-top: 10px;
    margin-left: 250px;
  }
.center {
   width: 70%;
   height: 30%;
   padding: 10px;
   margin-left: 17.5%;
   margin-right: 17.5%;
   padding-top: 150px;
   padding-bottom: 30px;
}
.categories{
  padding: 3px 7px;
  width: auto;
  color: #0084ff;
  border-radius: 15px;
  border: 1px solid #0084ff;
  margin: 5px;
  cursor: pointer;
  display: inline-block;
}
.categories:hover{
  background-color:#0084ff ;
  color: #fff;
}
.categoriescontainer{
  margin-bottom: 20px;
  font-size: 14.5px;
  padding-top: 16px;
}
.card-grid {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 72%;
  margin-right: 15%;
  margin-left: 15%;
  margin-top: 0px;  
}
.card {
  width: 30%;
  height: 400px;
  margin: 5px;
  border-radius: 5px;
  border: 0.5px solid #90b1cf;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 15px 25px, rgba(0, 0, 0, 0.05) 0px 5px 10px;
  background-color: white;

}
.card:hover{
  transform: scale(1.1);
}

.item {
  height: 100%;
  width: 100%;
  
}

.image-section {
  height: 176px;
  width: 100%;
}

.image-section img {
  width: 100%;
  height: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  
}

.text-section {
  padding: 10px 24px;
  text-align: left;
  
}

.title {
  font-size: 18px;
  font-weight: 600;
  margin: 0;
  
}

.author-container {
  padding-top: 4px;
  
}
.description{
  white-space: wrap;
  overflow: hidden;
  text-overflow: ellipsis;
  height: 135px;
}

.author {
  color: #0096ff;
  font-size: 14px;
  font-style: italic;
  font-weight: 500;
  }
footer {
  height: 200px;
  Background-color: #0084ff;
  padding-left: 280px;
  padding-top: 50px;
  color: #fff;
  margin-top: 50px;
}
*{
  margin: 0;
  padding: 0;
}
  

